import Mock from "mockjs";
import MockData from "../mock/data.js"
import Query from "../utils/query.js"

const singerInfoList = MockData.singerData.singerInfo

let listSingerInfo = Mock.mock('https://meta-mock/singerInfo/list', 'get', () => {

    return {
        code: 200,
        success: true,
        message: '获取所有歌手数据成功！',
        data: singerInfoList
    }
});

let detailSingerInfo = Mock.mock('https://meta-mock/singerInfo/detail', 'post', (options) => {

    const detailSingerInfoItem = JSON.parse(options.body);

    let queryList = []
    singerInfoList.forEach(
        function (item, index) {
            queryList.push(item.name)
        }
    )

    const detailNameReturn = Query.fuzzyQuery(queryList, detailSingerInfoItem.name)

    let detailSongInfoReturn = []

    for (let index in singerInfoList) {
        for (let key in detailNameReturn) {
            if (singerInfoList[index].name === detailNameReturn[key]) {
                detailSongInfoReturn.push(singerInfoList[index]);
            }
        }
    }


    return {
        code: 200,
        success: true,
        message: '获取歌手详情成功！',
        data: detailSongInfoReturn
    }
});


let addSingerInfo = Mock.mock('https://meta-mock/singerInfo/add', 'post', (options) => {

    const addSingerInfoItem = JSON.parse(options.body);

    const addDataGenerator = Mock.mock({
        'id': '@guid',
        'seq': '@increment',
    });
    addSingerInfoItem.id = addDataGenerator.id;
    addSingerInfoItem.seq = addDataGenerator.seq;

    singerInfoList.unshift(addSingerInfoItem);


    return {
        code: 200,
        success: true,
        message: '新增歌手数据成功！',
        data: singerInfoList
    }
})

let removeAllSingerInfo = Mock.mock('https://meta-mock/singerInfo/removeAll', 'post', (options) => {

    const removeAllSingerInfoItem = JSON.parse(options.body);

    removeAllSingerInfoItem.forEach((param) => {
        const findIndex = singerInfoList.findIndex((item) => {
            return item.id == param;
        });
        singerInfoList.splice(findIndex, 1);

    })

    return {
        code: 200,
        success: true,
        message: '批量删除歌手数据成功',
        data: singerInfoList
    }
})


let removeSingerInfo = Mock.mock('https://meta-mock/singerInfo/remove', 'post', (options) => {

    const removeSingerInfoItem = options.body;

    for (let index in singerInfoList) {
        if (singerInfoList[index].id === removeSingerInfoItem) {
            singerInfoList.splice(index, 1)
        }
    }

    return {
        code: 200,
        success: true,
        message: '删除歌手数据成功',
        data: singerInfoList
    }
})

let updateSingerInfo = Mock.mock('https://meta-mock/singerInfo/update', 'post', (options) => {

    const updateSingerInfoItem = JSON.parse(options.body);

    for (let index in singerInfoList) {
        if (singerInfoList[index].id === updateSingerInfoItem.id) {
            singerInfoList[index] = updateSingerInfoItem;
        }
    }

    return {
        code: 200,
        success: true,
        message: '更新歌手数据成功',
        data: singerInfoList
    }
})

export default {
    listSingerInfo,
    detailSingerInfo,
    addSingerInfo,
    removeSingerInfo,
    removeAllSingerInfo,
    updateSingerInfo
}