<template>

  <span>{{ baseMsg }}</span> / <b>{{ msg }}</b>

  <div>
    <br>
    <el-tabs type="card" v-model="activeTab">

      <el-tab-pane label="搜索歌手" name="search">
        <el-form :inline="true" :model="detailSingerInfo">
          <el-form-item label="歌曲姓名" required>
            <el-input v-model="detailSingerInfo.name" placeholder="请输入歌曲姓名(模糊查询)"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="detailItem()">搜索</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="新增入驻歌手" name="add">
        <el-form :inline="true" :model="addSingerInfo">
          <el-row>
            <el-col span="8">
              <el-form-item label="歌手姓名" :required="true">
                <el-input v-model="addSingerInfo.name" placeholder="请输入歌手姓名"/>
              </el-form-item>
              <el-form-item label="性别" :required="true">
                <el-input v-model="addSingerInfo.gender" placeholder="请输入性别"/>
              </el-form-item>
            </el-col>
          </el-row>


          <el-row>
            <el-col span="8">
              <el-form-item label="常驻城市" :required="true">
                <el-input v-model="addSingerInfo.address" placeholder="请输入常驻城市"/>
              </el-form-item>
              <el-form-item label="类型" :required="true">
                <el-input v-model="addSingerInfo.type" placeholder="请输入类型"/>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col span="8">
              <el-form-item label="星级" :required="true">
                <el-select v-model="addSingerInfo.star" placeholder="选择星级">
                  <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                  />
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="addItem()">增加</el-button>
                <el-button type="primary" @click="clearItem()">重置</el-button>

              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-tab-pane>
    </el-tabs>


    <br>
    <br>
    <hr>
    <el-table :data="listSingerInfo" style="width: 100%">
      <el-table-column prop="id" label="id" width="100" v-if="false"/>
      <el-table-column prop="seq" label="序号" width="100"/>

      <el-table-column prop="name" label="歌手姓名" width="100">
        <template v-slot="scope">
          <el-input type="text" v-model="scope.row.name" v-show="scope.row.isEdit"/>
          <span v-show="!scope.row.isEdit">{{ scope.row.name }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="gender" label="性别" width="100">
        <template v-slot="scope">
          <el-input type="text" v-model="scope.row.gender" v-show="scope.row.isEdit"/>
          <span v-show="!scope.row.isEdit">{{ scope.row.gender }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="address" label="常驻城市" width="300">
        <template v-slot="scope">
          <el-input type="text" v-model="scope.row.address" v-show="scope.row.isEdit"/>
          <span v-show="!scope.row.isEdit">{{ scope.row.address }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="type" label="类型" width="200">
        <template v-slot="scope">
          <el-input type="text" v-model="scope.row.type" v-show="scope.row.isEdit"/>
          <span v-show="!scope.row.isEdit">{{ scope.row.type }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="star" label="星级" width="100">
        <template v-slot="scope">
          <el-input type="text" v-model="scope.row.star" v-show="scope.row.isEdit"/>
          <span v-show="!scope.row.isEdit">{{ scope.row.star }}</span>
        </template>
      </el-table-column>


      <el-table-column fixed="right" label="操作" width="200">
        <template #default="scope">
          <el-button
              round
              type="primary"
              size="small"
              @click.prevent="editItem(scope)"
              v-show="!scope.row.isEdit"
          >
            编辑
          </el-button>
          <el-button
              round
              type="success"
              size="small"
              @click.prevent="updateItem(scope)"
              v-show="scope.row.isEdit"
          >
            更新

          </el-button>
          <el-button
              round
              type="danger"
              size="small"
              @click.prevent="removeItem(scope)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>


  </div>

</template>

<script>


import SingerApi from '../api/singer.js';
import Promise from '../utils/promise.js';
import Options from '../constants/select.js'

export default {
  name: "SingerView",
  props: {
    msg: String,
    baseMsg: String
  },
  data() {
    return {

      options: Options.singerPowerOptions,
      activeTab: "search",
      listSingerInfo: [],
      detailSingerInfo: {},
      addSingerInfo: {
        name: "",
        gender: "",
        address: "",
        type: "",
        star: ""
      }
    }
  },
  mounted() {

    Promise.ajax({
      "type": "get",
      "url": "https://meta-mock/singerInfo/list",
      "data": ""
    }).then(res => {
      // console.log(res.data);
      this.listSingerInfo = res.data;

    })

  },
  methods: {
    addItem: function () {

      // 新增数据简单校验
      for (const key in this.addSingerInfo) {
        if (!this.addSingerInfo[key]) {
          return this.$message({
            type: 'error',
            message: '请输入必填内容'
          })
        }
      }


      // console.log(this.addSingerInfo)
      Promise.ajax({
        "type": "post",
        "url": "https://meta-mock/singerInfo/add",
        "data": JSON.stringify(this.addSingerInfo)
      }).then(res => {
        // console.log(res.data);
        if (res.code == 200) {
          this.listSingerInfo = res.data;
          this.$message({
            type: 'info',
            message: res.message
          })
        }

      })

    },
    clearItem: function () {
      this.addSingerInfo = {
        name: "",
        gender: "",
        address: "",
        type: "",
        star: ""
      };
    },
    removeItem: function (scope) {

      this.$confirm('是否执行删除操作?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        Promise.ajax({
          "type": "post",
          "url": "https://meta-mock/singerInfo/remove",
          "data": scope.row.id
        }).then(res => {
          // console.log(res.data);
          if (res.code == 200) {
            this.listSingerInfo = res.data;
            this.$message({
              type: 'info',
              message: res.message
            })
          }

        })

      }).catch(() => {

      })

    },
    editItem: function (scope) {
      scope.row.isEdit = true;
    },
    updateItem: function (scope) {
      scope.row.isEdit = false;

      Promise.ajax({
        "type": "post",
        "url": "https://meta-mock/singerInfo/update",
        "data": JSON.stringify(scope.row)
      }).then(res => {
        // console.log(res.data);
        if (res.code == 200) {
          this.listSingerInfo = res.data;
          this.$message({
            type: 'info',
            message: res.message
          })
        }
      })

    },
    detailItem: function () {
      // console.log(this.detailSingerInfo)
      Promise.ajax({
        "type": "post",
        "url": "https://meta-mock/singerInfo/detail",
        "data": JSON.stringify(this.detailSingerInfo)
      }).then(res => {
        // console.log(res.data);
        if (res.code == 200) {
          this.listSingerInfo = res.data;
          this.$message({
            type: 'info',
            message: res.message
          })
        }
      })
    }
  }


}
</script>

<style scoped>

</style>